.hero {
  position: relative;
  z-index: 200;

  border-bottom: 1px solid rgb(150, 150, 150);
  box-shadow: 0 3px 6px rgba(0, 0, 0, 0.08), 0 3px 6px rgba(0, 0, 0, 0.12);

  &::before {
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;

    z-index: -1;

    content: '';

    background-color: rgb(245, 245, 245);
    background-size: cover;
    background-position: top;
    background-repeat: no-repeat;
  }
}

.hero-content {
  position: relative;

  display: grid;
  grid-auto-flow: row;
  gap: 2rem;

  padding: 4rem 1rem;

  @media(min-width: 80rem) {
    padding: 4rem 10rem;
  }
}

.hero-title {
  box-sizing: border-box;
  max-width: max-content;

  padding: 0.5rem 1.5rem;

  color: rgb(0, 0, 0);
  background: rgb(255, 222, 0);

  border-radius: 3px;

  font-size: 1.5rem;
  font-weight: 300;
  line-height: 1.5;

  @media(min-width: 80rem) {
    font-size: 3rem;
  }
}

.hero-title-emphasis {
  font-weight: 400;
}

.hero-subtitle {
  box-sizing: border-box;
  max-width: max-content;

  margin-top: 8rem;
  padding: 0.5rem 1.5rem;

  color: rgb(255, 255, 255);
  background: rgba(0, 0, 0, .5);

  border-radius: 3px;

  font-size: 1.3rem;
  font-weight: 300;
}

.hero-subtitle-emphasis {
  font-weight: 400;
}

.hero-actions {
  display: grid;
  grid-auto-flow: column;
  grid-template-columns: max-content;
  gap: 1rem;
}

.hero-action-item {
  display: inline-flex;
  flex-direction: row;
  align-items: center;

  padding: 1rem 2rem;

  border-radius: 3px;
  background: rgb(255, 222, 0);

  > .icon {
    margin-left: 1rem;
  }

  &,
  &:link,
  &:visited {
    color: rgb(0, 0, 0);
  }

  &:hover {
    text-decoration: none;
  }
}
