.card {
  display: block;

  margin: 2rem 0;
  padding: 2rem;

  background: rgb(245, 245, 245);

  @media(min-width: 80rem) {
    padding: 4rem;
  }

  &:first-child {
    margin-top: 0;
  }

  &:last-child {
    margin-bottom: 0;
  }
}

.card-actions-para {
  margin-top: 2rem;
}

.card-action {
  display: inline-flex;
  flex-direction: row;
  align-items: center;
  padding: 0.5rem 1rem;

  background: rgb(255, 222, 0);

  border-radius: 0.2rem;

  > .icon {
    margin-left: 1rem;
  }

  &,
  &:link,
  &:active,
  &:visited {
    color: black;
    text-decoration: none;
  }
}
