$primary-color: rgb(255, 222, 0);
$primary-color: #1b212f;

.button {
  display: inline-flex;
  flex-direction: row;
  align-items: center;

  padding: 0.5rem 1rem;

  white-space: nowrap;
  line-height: 2;

  text-transform: uppercase;
  text-decoration: none;

  color: black;
  background: rgb(255, 222, 0);
  background: rgb(210, 210, 210);

  border: none;
  border-radius: 0.2rem;

  white-space: nowrap;

  cursor: pointer;

  &,
  &:link,
  &:visited {
    color: black;
  }

  &:hover,
  &:focus {
    text-decoration: none;
    color: black;
    background: rgb(255, 222, 0);
  }

  > .icon {
    margin-left: 1rem;
  }
}

.button-primary {
  background: rgb(255, 222, 0);

  &,
  &:link,
  &:visited {
    color: black;
  }

  &:hover,
  &:focus {
    text-decoration: none;
    color: black;
  }
}

.button-danger {
  background: linear-gradient(180deg, lighten(red, 20%), red);

  &,
  &:link,
  &:visited {
    color: black;
  }

  &:focus {
    color: black;
  }
}

.button-outlined {
  border: 1px solid rgb(40, 40, 50);
  background: white;

  &:hover {
    border-color: transparent;
  }
}

.button:disabled {
  cursor: default;

  background: rgb(245, 245, 245);
  color: rgb(180, 180, 180);

  &:focus {
    box-shadow: none;
    outline: none;
  }
}

.button-bar {
  display: flex;
  flex-direction: row;

  > .button + .button {
    margin-left: 1rem;
  }
}
