.secondary-nav {
  position: relative;
  z-index: 100;

  border-bottom: 1px solid rgb(150, 150, 150);
  box-shadow: 0 3px 6px rgba(0, 0, 0, 0.08), 0 3px 6px rgba(0, 0, 0, 0.12);

  padding: 0 1rem;

  color: rgb(0, 0, 0);
  background: white;

  @media(min-width: 80rem) {
    position: sticky;
    top: 0;

    padding: 0 10rem;
  }
}

.secondary-nav-content {
  display: grid;
  grid-auto-flow: column;
  grid-template-columns: max-content;
  gap: 1rem;

  z-index: 999;
  background: white;
}

.secondary-nav-title {
  display: flex;
  flex-direction: row;
  align-items: center;
}

.secondary-nav-link {
  display: block;

  font-weight: 300;

  border-bottom: 1px solid transparent;

  &:hover {
    text-decoration: none;
    background-color: rgb(245, 245, 245);
  }
}

.secondary-nav-link-extra {
  @media(min-width: 80rem) {
    margin-left: auto;
  }
}

.secondary-nav-link-active {

  &,
  &:link,
  &:visited {
    background-color: rgb(245, 245, 245);
  }
}

.secondary-nav-overflow {
  position: absolute;
  top: 100%;
  left: 0;
  right: 0;

  display: none;
  grid-auto-flow: row;
  grid-template-rows: max-content;
  gap: 1rem;

  background: rgb(255, 255, 255);
  border-bottom: 1px solid rgb(150, 150, 150);
  box-shadow: 0 3px 6px rgba(0, 0, 0, 0.08), 0 3px 6px rgba(0, 0, 0, 0.12);

  z-index: 998;

  @media(min-width: 80rem) {
    position: relative;
    top: unset;
    left: unset;
    right: unset;

    display: grid;
    grid-auto-flow: column;
    grid-template-columns: max-content;
    gap: 1rem;
    align-items: center;

    padding: 0;

    border-bottom: none;
    box-shadow: none;
  }
}

.secondary-nav-nav {
  display: grid;
  grid-auto-flow: row;
  grid-template-rows: max-content;

  @media(min-width: 80rem) {
    display: grid;
    grid-auto-flow: column;
    grid-template-columns: max-content;
  }
}

.secondary-nav-link {
  padding: 0.5rem 1rem;
}

.secondary-nav-overflow-toggle {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;

  margin-left: auto;
  padding: 1rem 0;

  @media(min-width: 80rem) {
    display: none;
  }
}

.secondary-nav-overflow-toggle-icon {
  font-size: 1.75rem;
  transition: transform linear 0.25s;
}

#secondary-nav-overflow-toggle-checkbox:checked {
  & ~ .secondary-nav-overflow {
    display: grid;
  }

  & ~ .secondary-nav-overflow-toggle {
    > .secondary-nav-overflow-toggle-icon {
      transform: rotate(90deg);
    }
  }
}
