$primary-color: rgb(0, 100, 206);
$primary-color: #1b212f;
$primary-color: rgb(255, 222, 0);
$color-error: red;

.form {
  margin: 1rem 0;
}

.form-title {
  margin: 1rem 0;
  margin-top: 2rem;

  font-weight: 600;
}

.form-control {
  width: 100%;
  box-sizing: border-box;

  display: block;

  margin: 0.5rem 0;
  padding: 0.5rem;

  font-weight: normal;
  outline: none;

  color: black;
  background: white;

  border: 1px solid rgb(230, 230, 230);
  border-radius: 2px;

  &:focus {
    outline: none;
    border-color: $primary-color;
    box-shadow: 0 0 0 5px rgba($primary-color, 0.25);
    color: rgb(0, 0, 0);
  }

  &:disabled {
    color: rgb(100, 100, 100);
    background: rgb(245, 245, 245);

    border-color: rgb(200, 200, 200);
  }
}

.form-label {
  display: block;
  white-space: nowrap;
  font-weight: 600;
}

.form-group {
  display: block;
  margin: 1rem 0;

  &.ng-invalid {
    >.form-control {
      border-color: $color-error;
      box-shadow: 0 0 0 5px rgba($color-error, 0.1);
    }
  }
}

textarea.form-control {
  resize: vertical;
}

.form-checkbox-group {
  display: flex;
  flex-direction: row;
  align-items: center;

  margin: 0.5rem 0;

  >.form-label {
    margin-left: 0.5rem;

    font-weight: normal;
    white-space: unset;
  }
}

.form-help {
  margin-bottom: 0.5rem;
  font-size: 0.85rem;
  color: rgb(102, 102, 102);
}

.form-error {
  margin-bottom: 0.5rem;
  color: $color-error;
}

.form-buttons {
  display: flex;
  align-items: center;

  margin: 1rem 0;

  >.button+.dropdown,
  >.link+.dropdown,
  >.dropdown+.button,
  >.dropdown+.link,
  >.button+.button,
  >.button+.link,
  >.link+.button,
  >.link+.link {
    margin-left: 1rem;
  }

  .link:first-of-type {
    margin-left: auto;
  }
}

.form-buttons-align-right {
  justify-content: flex-end;
}

.form-grid {
  display: grid;
  grid-template-columns: min-content auto;
  gap: 1rem;
  align-items: baseline;

  >.form-group {
    display: contents;
  }

  >.form-title {
    grid-column: 1 / 3;
  }

  >.form-content,
  >.form-group>.form-content {
    grid-column: 1 / 3;
  }

  >.form-description,
  >.form-group>.form-description {
    grid-column: 2 / 3;
  }

  >.form-label,
  >.form-group>.form-label {
    grid-column: 1 / 2;
    text-align: right;
  }

  >.form-control,
  >.form-group>.form-control {
    grid-column: 2 / 3;
    margin: 0;
  }

  >.form-checkbox-group {
    display: contents;

    >.form-checkbox {
      grid-column: 1 / 2;
      margin-left: auto;
    }

    >.form-label {
      grid-column: 2 / 3;
      margin-left: 0;
    }
  }

  >.form-help {
    grid-column: 2 / 3;
    margin-top: 0;
    margin-bottom: 0;
  }

  >.form-error {
    grid-column: 2 / 3;
    margin-top: 0;
    margin-bottom: 0;
  }

  >.form-buttons {
    grid-column: 2 / 3;
    margin-top: 0;
    margin-bottom: 0;
  }
}

.form-narrow {
  max-width: 35rem;
}

.form-inline {
  display: flex;
  flex-direction: row;
  align-items: stretch;

  >.form-group {
    display: contents;
  }

  >.form-label,
  >.form-group>.form-label {
    align-self: center;
  }

  >.form-control,
  >.form-group>.form-control {
    width: auto;
    margin: 0;
  }

  >.form-label+.form-control,
  >.form-group>.form-label+.form-control {
    margin-left: 1rem;
  }

  >.form-group+.form-group>.form-label {
    margin-left: 1rem;
  }

  >.form-buttons {
    margin: 0;
    margin-left: 1rem;
  }
}

.form-inline-2 {
  display: grid;
  grid-auto-columns: auto;
  gap: 2rem;

  >.form-group {
    grid-row: 1;
    margin: 0;
  }
}

.form-card {
  padding: 1rem;
  background: rgb(248, 248, 248);
  box-shadow: rgba(0, 0, 0, 0.12) 0px 1px 3px, rgba(0, 0, 0, 0.24) 0px 1px 2px;
}
