.site-header {
  box-sizing: border-box;
  position: relative;
  z-index: 300;

  border-bottom: 1px solid rgb(150, 150, 150);
  box-shadow: 0 3px 6px rgba(0, 0, 0, 0.08), 0 3px 6px rgba(0, 0, 0, 0.12);

  background: rgb(255, 255, 255);
}

.site-header-content {
  position: relative;
  z-index: 301;
  box-sizing: border-box;

  display: grid;
  grid-auto-flow: column;
  grid-template-columns: max-content;
  gap: 4rem;
  align-items: center;

  padding: 1rem;

  background: white;

  @media(min-width: 80rem) {
    padding: 2rem 10rem;
  }
}

.site-header-overflow {
  z-index: 299;

  position: absolute;
  top: 100%;
  left: 0;
  right: 0;

  display: none;
  grid-auto-flow: row;
  grid-template-rows: max-content;
  gap: 1rem;

  padding: 1rem;

  background: rgb(255, 255, 255);

  border-bottom: 1px solid rgb(150, 150, 150);
  box-shadow: 0 3px 6px rgba(0, 0, 0, 0.08), 0 3px 6px rgba(0, 0, 0, 0.12);

  @media(min-width: 80rem) {
    position: relative;
    top: unset;
    left: unset;
    right: unset;

    display: grid;
    grid-auto-flow: column;
    grid-template-columns: max-content;
    gap: 1rem;
    align-items: center;

    padding: 0;

    border-bottom: none;
    box-shadow: none;
  }
}

.site-header-branding {
  color: rgb(0, 0, 0);
}

.site-header-nav {
  display: grid;
  grid-auto-flow: row;
  grid-template-rows: max-content;
  gap: 1rem;

  font-weight: 100;
  font-size: 1.2rem;

  @media(min-width: 80rem) {
    display: grid;
    grid-auto-flow: column;
    grid-template-columns: max-content;
    gap: 4rem;
  }

  .link {
    display: block;
  }

  .link:link,
  .link:active,
  .link:visited {
    color: black;

    font-weight: inherit;
    text-decoration: none;
  }
}

.site-header-actions {

  display: grid;
  grid-auto-flow: row;
  gap: 1rem;

  @media(min-width: 80rem) {
    grid-auto-flow: column;
    grid-template-columns: max-content;
    gap: 2rem;

    margin-left: auto;
  }
}

.site-header-action {
  display: inline-flex;
  flex-direction: row;
  align-items: center;
  padding: 0.5rem 1rem;

  background: rgb(255, 222, 0);

  border-radius: 0.2rem;

  > .icon {
    margin-left: 1rem;
  }

  &,
  &:link,
  &:active,
  &:visited {
    color: rgb(0, 0, 0);
    text-decoration: none;
  }
}

.site-header-overflow-toggle {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;

  margin-left: auto;
  padding: 1rem 0;

  @media(min-width: 80rem) {
    display: none;
  }
}

.site-header-overflow-toggle-icon {
  font-size: 1.75rem;
  transition: transform linear 0.25s;
}

#site-header-overflow-toggle-checkbox:checked {
  & ~ .site-header-overflow {
    display: grid;
  }

  & ~ .site-header-overflow-toggle {
    > .site-header-overflow-toggle-icon {
      transform: rotate(90deg);
    }
  }
}
