.subnav {
  display: grid;
  grid-auto-flow: column;
  grid-template-columns: max-content;
  gap: 1rem;
  align-items: stretch;

  padding: 0 2rem;

  background: white;

  @media(min-width: 80rem) {
    position: sticky;
    top: 0;

    padding: 0 10rem;
  }

  border-bottom: 1px solid rgb(150, 150, 150);
  box-shadow: 0 3px 6px rgba(0, 0, 0, 0.08), 0 3px 6px rgba(0, 0, 0, 0.12);
}

.subnav-title {
  display: flex;
  flex-direction: row;
  align-items: center;
}

.subnav-link {
  display: block;

  font-weight: 300;

  border-bottom: 1px solid transparent;

  &:hover {
    text-decoration: none;
    background-color: rgb(245, 245, 245);
  }
}

.subnav-link-active {

  &,
  &:link,
  &:visited {
    background-color: rgb(245, 245, 245);
  }
}

.subnav-overflow {
  display: flex;
  flex-direction: row;
  align-items: stretch;

  .subnav-link {
    padding: 0.5rem 1rem;
  }

  @media(min-width: 80rem) {}
}

.subnav-nav {
  display: grid;
  grid-auto-flow: column;
}
