.small-list {
  display: grid;
  grid-auto-flow: row;
  gap: 1rem;

  margin: 2rem 0;

  > li {
    align-self: stretch;
    justify-self: stretch;
    display: flex;
    flex-direction: row;
  }

  > li > i {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin-right: 1rem;

    font-size: 1.4rem;

    color: rgb(255, 222, 0);
  }
}

.small-list-heading {
  font-size: 1.3rem;
}
