.team-member {

  @media(min-width: 60rem) {
    display: flex;
    flex-direction: row-reverse;
  }
}

.team-member-image-container {
  margin-bottom: 2rem;

  @media(min-width: 60rem) {
    margin-bottom: 0;
    margin-left: 2rem;
  }
}

.team-member-image {
  width: 100%;

  border-radius: 3px;
  border: 1px solid rgb(0, 0, 0);
  box-shadow: 0 0 5px 4px rgba(130, 130, 130, 0.25);

  @media(min-width: 60rem) {
    width: 30rem;
  }
}

.team-member-description {
  flex: 1;
}
